.products{
    display: grid;
    flex-direction: column;
    justify-content: center;
    margin: 1rem 0;
    font-size: .9rem;
}

.products .productsContainer{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
}

#bottomText{
    margin: 2rem 0 6rem 0;
}

@media (max-width: 800px) {
    .products .productsContainer{
        display: grid;
        grid-template-columns: 1fr;
        grid-auto-rows: auto;
    }
}
