.navbar {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    padding: 0.2rem;
    background: linear-gradient(to right, #dddddd, #f0f0f0, #dddddd);
    font-size: 0.9rem;
}

.navbar ul li {
    list-style-type: none;
    display: inline-block;
    margin-right: 1.3rem;
    position: relative;
    font-weight: 700;
}

.smallList {
    display: none;
    max-height: 0;
    overflow: hidden;
    opacity: 0;
}

.smallList.active {
    display: none;
    max-height: 600px; /* Adjust this as needed */
    opacity: 1;
}

.smallList ul li {
    display: none;
    list-style-type: none;
    text-decoration: none;
    font-weight: 700;
}

.navbar ul li:hover {
    color: black;
    cursor: pointer;
    transition: 300ms;
}

.navbar ul li:not(hover) {
    color: #393939;
    transition: 600ms;
}

.navbar ul li::after {
    content: '';
    position: absolute;
    left: 50%;
    bottom: 0;
    width: 0;
    height: 1px;
    background-color: black;
    transition: width 300ms ease-in-out, left 300ms ease-in-out;
}

.navbar ul li:hover::after {
    width: 100%;
    left: 0;
}

.flagContainer img {
    height: 13px;
    margin: 3px;
}

.flagContainer img:hover {
    cursor: pointer;
}

.burger {
    display: none;
}

@media (max-width: 576px) {
    .languageSelectorDiv, .navbarList {
        display: none;
    }

    .navbar {
        justify-content: space-between;
        padding: 0 2.5rem;
    }

    .burger {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        width: 30px;
        height: 20px;
        cursor: pointer;
    }

    .burger div {
        width: 100%;
        height: 3px;
        background-color: #333;
        border-radius: 2px;
    }

    .smallList {
        display: flex;
        background-color: #6a6a6a;
        flex-direction: column;
        align-items: center;
        transition: max-height 0.3s ease-in-out, opacity 4000ms ease-in-out;
    }

    .smallList ul {
        width: 100%;
    }

    .smallList ul li {
        padding: 10px 20px;
        color: white;
        display: flex;
    }

    .smallList ul li:hover {
        background-color: #4a4a4a;
    }

    .smallList.active {
        display: block;
    }

}
